
.point-list {
    padding: 20px;
    justify-content: left;
    display: flex;
    flex-direction: row;
    max-height: 85vh;
    border-radius: 6px;
    background-color: #299fce;
  }

.add-button {
  background-color: #1089b9;
  font-size: calc(8px + 2vmin);
}

.unhighlighted-list {
  display: grid;
  grid-template-columns: repeat(3, 18vw);
}